import { type ZodObject } from "zod";
import * as z from "zod";
import type { SmartFormFieldsMetadata } from "../SmartForm/common.js";

export const createCommonDataSourceSchema = <
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  Schema extends ZodObject<any>,
>(
  configSchema: Schema,
) =>
  z.object({
    name: z.string().trim().min(1),
    prefix: z
      .string()
      .trim()
      .min(1)
      .regex(
        /^$|^[A-Z][A-Za-z0-9_]*$/,
        "Must start with an uppercase letter and only consist of alphanumeric characters and underscores",
      ),
    config: configSchema,
  });

export const createCommonDataSourceFieldsMeta = (
  configFieldsMeta: SmartFormFieldsMetadata,
) => ({
  name: {
    label: "Data Source Name",
    name: "name",
  },
  prefix: {
    label: "Data Source Prefix",
    helpText:
      "The prefix to use for types synced from this data source. It must start with an uppercase letter and can only consist of alphanumeric characters and underscores. For example, Product becomes {Prefix}Product.",
  },
  config: configFieldsMeta,
});
